import { isNumeric } from './index';

const DEFS = Object.seal({}); // faster, see http://jsben.ch/GRZ7q

export default function validateDate (d = DEFS, props, path, output) {
    const { name, transform, required, nextValidator } = props;
    const { day, month, year } = d;

    if (day && !isNumeric(day)) {
        return { name, path, required, error: 'invalidDay' };
    }
    if (month) {
        const m = parseInt(month,10);
        if (!isNumeric(month) || m<1 || m>12) {
            return { name, path, required, error: 'invalidMonth' };
        }
    }
    if (year && !isNumeric(year)) {
        return { name, path, required, error: 'invalidYear' };
    }

    if (day && month && year) {
        // todo is it a valid date
        if (nextValidator) {
            const r = nextValidator(d,props,path,output);
            if (r) return r;
        }
        if (output) {
            const date = {day, month, year};
            output[name] = transform ? transform(date) : date;
        }
        return true;
    }

    if (required) {
        return { name, path, required, error: 'dateRequired' };
    }
}

