import React from "react";
import {connect} from "react-redux";
import {lookup} from "../startRedux";
import {makeChildField, updateRedux} from "./NoboField";
// components
import Input from "../components/Input";
import Select from "../components/Select";
import InputPhone from "../components/InputPhone";
import InputDate from "../components/InputDate";
import validateDate from '../validation/validateDate';

const nameProps = { id: 'fullName', component: Input, minLength: 6, maxLength: 40, required: true };
const genderProps = { id: 'gender', component: Select, options: 'gender', required: true };
const dobProps = { id: 'dateOfBirth', component: InputDate, useMonthSelect: true, required: true, name: 'dateOfBirth', myValidator: validateDate };
const dateOfGrad = { id: 'dateOfGrad', component: InputDate, name: 'dateOfGrad', myValidator: validateDate };
const phoneProps = { id: 'phone', component: InputPhone };
const emailProps = { id: 'email', component: Input, minLength: 8, maxLength: 80, validator: checkEmail };

export class SimplePersonalDetails extends React.PureComponent {

    constructor(props) {
        super(props);
        if (this.props.parentForm) {
            this.props.parentForm.sections[this.props.id] = this;
        }
    }

    getId () { return this.props.id; }

    validateSection () {
        const values = this.props.values;
        const errs = [];
        function vx (fieldProps) {
            const err = valido(values,fieldProps);
            errs.push(err);
        }
        vx(nameProps);
        vx(genderProps);
        vx(dobProps);
        vx(phoneProps);
        vx(emailProps);
        console.log(errs);
    }

    validationCode (values,f) {
        f(nameProps);
        f(genderProps);
        f(dobProps);
        f(phoneProps);
        f(emailProps,{ required: values.gender === 'F' });
    }

    onTest = (errs,output) => {
        const values = this.props.values;
        this.validationCode(values,(fProps,extras) => {
            const data = values[fProps.id];
            const vd = fProps.component.defaultValidator;
            const error =  vd(data,{...fProps,...extras});
            if (error) errs.push(error);
        });
        const id = this.props.id;
        output[id] = {}; // sanitized values
    };

    FullName = makeChildField(this, nameProps);
    Gender = makeChildField(this, genderProps);
    DOB = makeChildField(this, dobProps);
    Phone = makeChildField(this, phoneProps);
    Email = makeChildField(this, emailProps);
    DOG = makeChildField(this, dateOfGrad);

    render () {
        const { values } = this.props;
        const { FullName, Gender, DOB, Phone, Email, DOG } = this;
        return (
            <div id={this.props.id}>
                <FullName/>
                <Gender/>
                <DOB/>
                <DOG/>
                <Phone/>
                <Email required={values.gender==='F'} />
            </div>
        );
    }
}

function mapStateToProps (state,props) {
    const values = lookup([props.id],state.formState);
    return {
        values,
        coreData: state.formState.coreData
    };
}

export default connect(mapStateToProps,{updateRedux},null,{ withRef: true })(SimplePersonalDetails);
// export default connect(mapStateToProps,{updateRedux})(SimplePersonalDetails);


function checkEmail (value = '') {
    if (value.indexOf('.')<0 || value.indexOf('@')<0)
        return 'badEmail';
}

function valido(values, props) {
    const val = values[props.id];
    const err = props.component.defaultValidator(val,props);
    console.log(props.id,err,val);
    return { [props.id]: err };
}



