
let fetchInProgress = false;

export function fetchTime (/* evtStart, evtSuccess, evtError */) {
    if (fetchInProgress)
        return;
    fetchInProgress = true;

    const url = '/time';
    const prox = '';

    const onError = (err) => {
        fetchInProgress = false;
        console.log('CAUGHT ERROR',err);
    };

    const onSuccess = (res) => {
        fetchInProgress = false;
        console.log( res.status, res.headers );

        const ct = res.headers.get('Content-Type');

        if (ct.indexOf('text/xml')===0) {
            //console.log('WE GOD BOD',res.text());
            res.text().then( (text) => {
                console.log('WE GOD TEXT',text);
            }).catch(onError);
            return;
        }

        res.json().then( (js) => {
            console.log('WE GOD JSON:',js);
        }).catch(onError);
    };

    console.log('fetch',prox + url);
    fetch(prox + url)
        .then(onSuccess)
        .catch(onError);
}



