
let fetchInProgress = false;

export function fetchRates (/* evtStart, evtSuccess, evtError */) {
    if (fetchInProgress)
        return;
    fetchInProgress = true;

    //const url = 'http://www.ecb.europa.eu/stats/eurofxref/eurofxref-daily.xml';
    //const prox = 'https://cors-anywhere.herokuapp.com/';
    //const url = 'https://data.fixer.io/api/latest';
    // const url = '/static/media/logo.5d5d9eef.svg';
    const url = '/eurofxref.xml';
    const prox = '';

    const onError = (err) => {
        fetchInProgress = false;
        console.log('CAUGHT ERROR',err);
    };

    const onSuccess = (res) => {
        fetchInProgress = false;
        // console.log( res.status, JSON.stringify(res.headers) );
        console.log( res.status, res.headers );
        /*
        res.headers.keys().forEach( key => {
            console.log(key,res.headers.get(key));
        });
        console.log( JSON.stringify(res.headers), res.headers.get('Content-Type'), res );
        for(let key of res.headers.keys()) {
            console.log(key,res.headers.get(key));
        }
        console.log( JSON.stringify(res.headers), res.headers.get('Content-Type'), res );
        */

        // if (res) throw( new Error('Zarg!') );

        const ct = res.headers.get('Content-Type');

        if (ct.indexOf('text/xml')===0) {
            //console.log('WE GOD BOD',res.text());
            res.text().then( (text) => {
                console.log('WE GOD TEXT',text);
            }).catch(onError);
            return;
        }

        res.json().then( (js) => {
            console.log('WE GOD JSON:',js);
        }).catch(onError);
    };

    console.log('fetch',prox + url);
    fetch(prox + url)
        .then(waitFor(1000))
        .then(onSuccess)
        .catch(onError);
}

const waitFor = (delay) => (result) => {
    return new Promise( resolve => {
        setTimeout( () => {
            return resolve(result);
        },delay);
    });
};

