import React from "react";
import logo from '../logo.svg';
import SimplePersonalDetails from './SimplePersonalDetails';
import store from '../startRedux';
import Select from "../components/Select";
import { FormFieldRender } from "./NoboField";
import { setLanguage, translate } from "../Translator";
import CheckBox from "../components/CheckBox";
import { fetchRates } from './fetchRates';
import { fetchTime } from './fetchTime';
import { idNumber } from '../common/IdentificationSection'
import PageLink from "../hack/PageLink";
import ReadItOut from "../components/ReadItOut";

console.log('LOADED FourthPage',idNumber);

export default class FourthPage extends React.Component {

    constructor(props) {
        super(props);
        this.sections = {};
        this.F2 = React.createRef();
    }

    componentDidMount () {
        console.log('componentDidMount FourthPage');
        // document.body.focus();
    }

    valida1 = () => {
        this.sections.DT1.validateSection();
        const f = this.F2.current.getWrappedInstance();
        f.validateSection();
        const state = store.getState();
        console.log('STATE',state);
        //store.dispatch()
        store.dispatch({ type: 'SETPATH', key: ['DT1','fullName'], value: String(new Date()) });
    };

    render () {
        return (
            <div className="App">
                <header className="App-header">
                    <img src={logo} className="App-logo" alt="logo"/>
                    <h1 className="App-title">Fourth Page</h1>
                    <ReadItOut>This is the fourth and final page</ReadItOut>
                </header>

                <div>
                    <button onClick={this.valida1}>ValidateX</button>{' '}
                    <button onClick={fetchRates}>Call server</button>{' '}
                    <button onClick={fetchTime}>Call server time</button>
                </div>
                <div className="form">
                    <FormFieldRender id={'showAllErrors'} sectionId={'coreData'} compo={CheckBox} noLabel />
                    <FormFieldRender id={'lang'} sectionId={'coreData'} compo={Select} options="country" onChange={setLanguage}/>
                    <h4>{translate('Your Details')}</h4>
                    <SimplePersonalDetails parentForm={this} id="DT1"/>
                    <h4>{translate('Employment Details')}</h4>
                    <SimplePersonalDetails id="DT2" ref={this.F2}/>
                    <PageLink url="/zooter" ttl="Home Page"/>
                    <PageLink url="/zooter/2" ttl="2nd Page"/>
                    <PageLink url="/zooter/3" ttl="3rd Page"/>
                </div>
            </div>
        );
    }

}



