import {stringTypeField} from "../validation/validateString";
import {optionTypeField} from "../validation/validateOption";

export const country = optionTypeField('country', {options: 'country', required: true});
export const idNumber = stringTypeField('idNumber', {maxLength: 20, required: true});

function validateIdSection(v) { // values, sectionProps, output, errors, path
    v(country);
    v(idNumber);

    let { x, y, ...z } = { x: 1, y: 2, a: 3, b: 4 };
    console.log(x); // 1
    console.log(y); // 2
    console.log(z); // { a: 3, b: 4 }
}

const idSection = {validateSection: validateIdSection};

export default idSection;

// end of



