import React from 'react';

export default function PhoneInput ({ value = {}, id, required, invalid, onStateChange, onBlur, ariaDescribedBy }) {

    const onChangeNumber = (ev) => {
        const mod = { country: value.country, phone: ev.target.value };
        // onChange(mod);
        onStateChange({ value: mod });
    };

    const cnm = invalid ? 'input-telephone input-invalid' : 'input-telephone';
    const ariaLabel = undefined;

    return (
        <div className="input-telephone-div">
            <input
                aria-describedby={ariaDescribedBy}
                aria-invalid={invalid}
                aria-label={ariaLabel}
                className={cnm}
                id={id}
                maxLength={10}
                onBlur={onBlur}
                onChange={onChangeNumber}
                placeholder={'telephone'}
                required
                title={required ? 'Must enter' : 'Optional field'}
                type="tel"
                value={value.phone || ''}
            />
        </div>
    );
}

PhoneInput.defaultValidator = function (value, props) {
    if (!value || !value.phone) {
        return;
    }
    const reg = /^\d+$/;
    if (!reg.test(value.phone))
        return 'numeric';
};



