
/** for option lists, ie <select>
 *  props.options can (optionally) contain a map of valid options
 *  valid options should map to something truthy,
 *  so this can be: { M: 'Male', F: 'Female' } - anything else is invalid
 * */

export default function validateOption (value, props, path, output) {
    const {name, required, options, excludedOptions} = props;

    if (value) {
        if ((options && !options[value]) || (excludedOptions && excludedOptions[value])) {
            return {name, path, required, error: 'invalidOption'};
        }
        if (output) {
            output[name] = value;
        }
        return true;
    }

    if (required) {
        return {name, path, required, error: 'required'};
    } else {
        if (output) {
            output[name] = '';
        }
        return true;
    }
}

export const optionTypeField = (name,props) => { return { name, ...props, validator: validateOption } };



